@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2");
  src: url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2");
  src: url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  width: 320px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1150px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0;
  min-width: 320px;
  background-color: #ffffff;
}

.page__container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden;
}

.button {
  border: 2px solid;
  text-transform: uppercase;
  font-family: "Open Sans", "Arial", sans-serif;
  cursor: pointer;
}
.button:focus {
  outline: 2px solid #63d1db;
}

.button--ghost {
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  background-color: #ffffff;
  border-color: #444444;
}
.button--ghost:hover {
  color: #ffffff;
  background-color: #444444;
}
.button--ghost:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}

.button--green {
  color: #ffffff;
  border-color: #62d1bb;
  background-color: #63d1bb;
}

.main-container {
  flex-grow: 1;
}
@media (max-width: 767.98px) {
  .main-container {
    padding-top: 80px;
  }
}

.main-header {
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}

@media (max-width: 767.98px) {
  .main-header {
    background-image: linear-gradient(to top, #e6e6e6 1px, #ffffff 1px);
    position: absolute;
  }

  .main-header--nojs.main-header {
    position: relative;
    margin-bottom: -80px;
  }
}
@media (min-width: 768px) {
  .main-header__logo {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .main-header__logo {
    width: 149px;
    height: 72px;
  }
}
.main-nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  min-height: 79px;
  padding-top: 12px;
  z-index: 2;
}

.main-nav__logo-wrap {
  align-self: center;
  padding: 10px 0 18px;
  position: relative;
  max-width: 90px;
}

.main-nav__toggle {
  position: absolute;
  right: -86px;
  top: 22px;
  width: 20px;
  height: 14px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 6px 0 0 #231f20, 0 12px 0 0 #231f20;
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 2px;
  left: -2px;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

.main-nav__icon {
  position: absolute;
  left: -45px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.2;
}

.main-nav__link {
  position: relative;
}
.main-nav__link:not(.current):hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link:not(.current):hover .main-nav__icon {
  fill: #63d1bb;
  opacity: 1;
}
.main-nav__link:not(.current):active {
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__link:not(.current):active .main-nav__icon {
  fill: #63d1bb;
  opacity: 0.3;
}

.main-nav__item {
  padding: 22px 15px;
  width: 320px;
  min-height: 79px;
}

.main-nav__text-wrap {
  order: 5;
  display: none;
}

.main-nav__text {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  margin: 0;
}

.main-nav__logo:not(.current):hover {
  opacity: 0.6;
}
.main-nav__logo:not(.current):active {
  opacity: 0.3;
}

@media (max-width: 767.98px) {
  .main-nav--closed .main-nav__list--mobile {
    display: none;
  }

  .main-nav--opened .main-nav__list--site {
    background-color: #ffffff;
    z-index: 2;
    left: 0;
  }

  .main-nav--opened .main-nav__list--user {
    background-color: #ffffff;
    z-index: 2;
    left: 0;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__list--mobile {
    position: static;
    display: block;
    background-color: #ffffff;
  }

  .main-nav {
    width: 100vw;
    padding: 12px 0 0;
  }

  .main-nav__item {
    width: 320px;
    padding: 27px 15px 27px 76px;
    margin: 0 auto;
    position: relative;
  }
  .main-nav__item::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 98vw;
    background-color: #ececec;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch;
    padding-top: 0;
  }

  .main-nav__logo-wrap {
    align-self: center;
    padding: 8px 0 0;
  }

  .main-nav__link--search span {
    font-size: 0;
    opacity: 0;
    word-break: break-all;
  }
  .main-nav__link--search .main-nav__icon {
    left: 32px;
    top: 20px;
  }
  .main-nav__link--cart .main-nav__icon {
    left: -51px;
  }

  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 380px;
    min-height: 88px;
  }

  .main-nav__item {
    min-height: 88px;
    padding: 22px 0;
    width: 140px;
  }

  .main-nav__text-wrap {
    display: block;
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f1f1f1 1px);
    flex-grow: 1;
    position: relative;
    padding-top: 21px;
  }
  .main-nav__text-wrap::after {
    content: "";
    position: absolute;
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f1f1f1 1px);
    width: calc((100vw - 668px) / 2);
    height: 100%;
    top: 0;
    z-index: -1;
    right: calc((100vw - 668px) / 2 * -1);
  }
  .main-nav__text-wrap--index {
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f9f9f9 1px);
  }
  .main-nav__text-wrap--index::after {
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f9f9f9 1px);
  }

  .main-nav__text {
    text-align: end;
    max-width: 338px;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__logo-image {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .main-nav__item {
    width: 160px;
    padding: 20px 0;
  }
  .main-nav__item--catalog {
    width: 185px;
  }

  .main-nav__list {
    display: flex;
    justify-content: space-between;
  }

  .main-nav__text {
    display: block;
    order: 5;
    max-width: 670px;
  }
  .main-nav__text::after {
    content: "";
    position: absolute;
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f9f9f9 1px);
    width: calc((100vw - 1050px) / 2);
    top: 0;
    bottom: 0;
    z-index: -1;
    right: calc((100vw - 1050px) / 2 * -1);
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__logo-wrap {
    order: 2;
    padding: 8px 0 0;
    align-self: flex-start;
    max-width: 150px;
  }

  .main-nav__logo-image {
    width: 149px;
    height: 72px;
  }
}
.site-list__link {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 768px) {
  .site-list__link {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .site-list__link {
    font-size: 17px;
  }
}

@media (min-width: 768px) {
  .site-list {
    order: 3;
    width: 330px;
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f1f1f1 1px);
    position: relative;
    justify-content: space-between;
  }
  .site-list::before {
    content: "";
    position: absolute;
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f1f1f1 1px);
    height: 100%;
    width: calc((100vw - 668px) / 2);
    left: calc((100vw - 668px) / 2 * -1);
    z-index: -1;
  }
  .site-list--index {
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f9f9f9 1px);
  }
  .site-list--index::before {
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f9f9f9 1px);
  }
}
@media (min-width: 1150px) {
  .site-list {
    width: 377px;
    order: 1;
    position: relative;
    background-color: inherit;
    background-image: none;
  }
  .site-list::before {
    content: none;
  }
  .site-list::after {
    content: "";
    width: 1px;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    position: absolute;
    right: 51%;
  }
}

@media (max-width: 767.98px) {
  .site-list__item:first-child::before {
    content: "";
    position: absolute;
    height: 1px;
    width: 98vw;
    background-color: #ececec;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (min-width: 768px) {
  .site-list__item {
    order: 3;
    width: 161px;
  }
}
@media (min-width: 1150px) {
  .site-list__item {
    padding-top: 33px;
    width: 157px;
  }
}

.user-list__link {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
  order: 2;
  display: block;
}

.user-list__icon-links,
.user-list__text-links {
  list-style-type: none;
  padding: 0;
}

@media (min-width: 768px) {
  .user-list {
    justify-content: space-between;
    position: relative;
    width: 346px;
  }

  .user-list__wrap {
    position: relative;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  .user-list__wrap--icon {
    max-width: 100px;
  }
  .user-list__wrap--icon::before {
    content: "";
    width: 1px;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    position: absolute;
    top: 0;
    left: 0;
  }
  .user-list__wrap--icon::after {
    content: "";
    width: 1px;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    position: absolute;
    top: 0;
    right: 0;
  }

  .user-list__item-search {
    width: 82px;
    padding: 25px 0;
    text-align: center;
    position: relative;
  }

  .user-list__item-cart {
    padding: 32px 0 15px 63px;
    width: 245px;
  }
}
@media (min-width: 1150px) {
  .user-list {
    order: 3;
    width: 380px;
    padding-left: 31px;
  }
}
.additional-list {
  order: 4;
  display: none;
}

.additional-list__link {
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}

@media (min-width: 1150px) {
  .additional-list {
    display: flex;
    justify-content: flex-start;
    width: 380px;
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f1f1f1 1px);
    position: relative;
  }
  .additional-list::before {
    content: "";
    position: absolute;
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f1f1f1 1px);
    height: 100%;
    width: calc((100vw - 1050px) / 2);
    left: calc((100vw - 1050px) / 2 * -1);
    z-index: -1;
  }
  .additional-list--index {
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f9f9f9 1px);
  }
  .additional-list--index::before {
    background-image: linear-gradient(to bottom, #e6e6e6 1px, #f9f9f9 1px);
  }

  .additional-list__item:not(:last-child) {
    margin-right: 60px;
  }
}
.main-title {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: #000000;
  background-color: #f1f1f1;
  min-height: 177px;
  padding-top: 73px;
  padding-bottom: 40px;
  position: relative;
}
.main-title--index {
  background-image: url("../img/hero-mobile.webp");
  background-repeat: no-repeat;
  background-position: center -17px;
  background-color: #f9f9f9;
  min-height: 488px;
  padding: 71px 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-resolution: 2dppx) {
  .main-title--index {
    background-image: url("../img/hero-mobile@2x.webp");
    background-size: 320px auto;
  }
}

.main-title__text {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: #000000;
  text-align: center;
  margin: 0 auto;
  padding: 0 30px 15px;
  width: 320px;
}

@media (min-width: 768px) {
  .main-title__text {
    font-weight: 700;
    font-size: 43px;
    line-height: 50px;
    width: 768px;
    padding: 0 50px 15px;
  }

  .main-title {
    min-height: 138px;
    padding-top: 14px;
  }
  .main-title--index {
    min-height: 658px;
    background-image: url("../img/hero-desktop.webp");
    background-position: bottom;
    padding-top: 68px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .main-title--index {
    background-image: url("../img/hero-desktop@2x.webp");
    background-size: 768px auto;
  }
}
@media (min-width: 768px) {
  .main-title::after {
    content: "";
    position: absolute;
    bottom: -12px;
    background-image: url("../img/zigzag-fill-tablet.svg");
    background-repeat: repeat-x;
    width: 100%;
    height: 24px;
  }
}
@media (min-width: 1150px) {
  .main-title__text {
    font-weight: 700;
    font-size: 53px;
    line-height: 57px;
    width: 1150px;
  }

  .main-title {
    min-height: 184px;
  }
  .main-title::after {
    background-image: url("../img/zigzag-fill-desktop.svg");
    background-size: 1150px 34px;
    background-position-x: center;
    height: 34px;
    bottom: 0;
  }
  .main-title--index {
    min-height: 675px;
    padding: 70px 0 17px;
    background-position: center -89px;
  }
  .main-title--index::after {
    background-size: 1250px;
  }
}
.catalog-navigation {
  padding: 0;
  margin: auto;
  margin-bottom: 0;
}

.catalog-navigation__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin: 0 auto;
}

.catalog-navigation__link {
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  text-decoration: none;
  width: 100vw;
  min-height: 103px;
  display: block;
}

.catalog-navigation__link-icon {
  opacity: 0.5;
}
.catalog-navigation__link-icon--toy {
  transform: translate(2px, -3px);
}

.catalog-navigation__link-content {
  width: 320px;
  display: grid;
  grid-template-columns: 4fr 1fr;
  padding: 24px 30px 17px;
  gap: 25px;
}

.catalog-navigation__link--lighten {
  background-color: #63d1bb;
}
.catalog-navigation__link--lighten:hover {
  background-color: #59d5bc;
  transform: translateY(-5px);
  box-shadow: 0 5px #59d5bc;
  transition: transform 250ms;
}
.catalog-navigation__link--lighten:hover .catalog-navigation__link-icon {
  opacity: 0.7;
}
.catalog-navigation__link--lighten:active {
  background-color: #59d5bc;
}
.catalog-navigation__link--lighten:active .catalog-navigation__link-icon {
  opacity: 0.3;
}

.catalog-navigation__link--darken {
  background-color: #56c3b2;
}
.catalog-navigation__link--darken:hover {
  background-color: #46c1ae;
  transform: translateY(-5px);
  box-shadow: 0 5px #46c1ae;
  transition: transform 250ms;
}
.catalog-navigation__link--darken:hover .catalog-navigation__link-icon {
  opacity: 0.7;
}
.catalog-navigation__link--darken:active {
  background-color: #46c1ae;
}
.catalog-navigation__link--darken:active .catalog-navigation__link-icon {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .catalog-navigation__list {
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 0;
    width: 668px;
  }

  .catalog-navigation__link {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    width: 334px;
    min-height: 119px;
  }

  .catalog-navigation__link-icon {
    transform: translate(5px, 7px);
  }
}
@media (min-width: 1150px) {
  .catalog-navigation__list {
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 0;
    width: 1050px;
  }

  .catalog-navigation__link {
    width: 525px;
    min-height: 119px;
  }

  .catalog-navigation__link-content {
    width: 513px;
    grid-template-columns: 10fr 1fr;
    padding: 24px 30px 30px;
    gap: 30px;
  }

  .catalog-navigation__link-icon {
    transform: translate(0, 7px);
  }
}
.product-week {
  position: relative;
}
.product-week::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 6px;
  background-image: url("../img/zigzag-line-mobile.svg");
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
}

.product-week__wrapper {
  padding-top: 30px;
  display: grid;
  grid-template-rows: repeat(7, auto);
  row-gap: 5px;
  min-height: 608px;
  padding-bottom: 41px;
  position: relative;
}

.product-week__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
}

.product-week__subtitle {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
  margin: 0;
}

.product-week__description {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  margin: 13px 0;
}

.product-week__price {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  text-align: center;
  margin-bottom: 19px;
}

.product-week__button {
  width: 260px;
  min-height: 52px;
  text-decoration: none;
  text-align: center;
  padding-top: 14px;
}

.product-week__photo {
  margin-top: 22px;
  margin-bottom: 17px;
}

@media (min-width: 768px) {
  .product-week {
    padding-bottom: 18px;
  }
  .product-week::after {
    content: "";
    position: absolute;
    background-image: url("../img/zigzag-line-tablet.svg");
    width: 100%;
    height: 17px;
  }

  .product-week__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: repeat(5, auto);
    padding-top: 50px;
  }

  .product-week__title {
    font-weight: 700;
    font-size: 43px;
    line-height: 50px;
    color: #000000;
    margin-bottom: 32px;
    grid-column: 1/4;
    grid-row: 1/2;
  }

  .product-week__subtitle {
    font-weight: 700;
    font-size: 17px;
    line-height: 17px;
    color: #63d1bb;
    text-transform: uppercase;
    margin: 0;
    padding-left: 42px;
    padding-top: 8px;
    grid-column: 4/5;
    grid-row: 1/2;
    text-align: center;
